<template>
  <v-card flat class="mx-auto mt-5">
    <v-data-table
      :hide-default-footer="true"
      disable-pagination
      disable-filtering
      disable-sort
      :headers="headers"
      :items="salas"
    >
      <template v-slot:top>
        <v-toolbar flat>
          <v-toolbar-title>Turmas</v-toolbar-title>
          <v-spacer></v-spacer>
          <v-col cols="3">
            <v-select
              v-if="_turnos != null && _turnos.length > 1"
              v-model="turnoSelecionado"
              :items="_turnos"
              item-text="name"
              item-value="id"
            />
          </v-col>
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn
                color="primary"
                class="mb-2"
                v-bind="attrs"
                v-on="on"
                :disabled="turnoSelecionado === null || locked"
              >
                Nova turma
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>

              <v-card-text>
                <v-container>
                  <v-form @submit.prevent ref="form">
                    <v-row>
                      <v-col cols="6" sm="6">
                        <v-text-field
                          v-model="editedItem.name"
                          label="Nome da sala"
                          :rules="[rules.required]"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="6" sm="6">
                        <v-text-field
                          v-model="editedItem.dias_sem_ultima_aula"
                          label="Dias sem última aula"
                          type="number"
                          min="0"
                          :rules="[
                            rules.required,
                            rules.greaterOrEqualThanZero,
                          ]"
                        ></v-text-field>
                      </v-col>
                      <v-col cols="6" sm="6">
                        <v-text-field
                          v-model="editedItem.ordem"
                          label="Ordenação"
                          type="number"
                          :rules="[
                            rules.required,
                            rules.greaterOrEqualThanZero,
                          ]"
                        ></v-text-field>
                      </v-col>
                    </v-row>
                  </v-form>
                </v-container>
              </v-card-text>

              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancelar
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> OK </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5"
                >Tem certeza que deseja excluir essa sala?
              </v-card-title>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancelar</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>
        </v-toolbar>
      </template>
      <!-- eslint-disable-next-line -->
        <template v-slot:item.actions="{ item }">
        <v-icon small class="mr-2" @click="editItem(item)" :disabled="locked">
          mdi-pencil
        </v-icon>
        <v-icon small @click="deleteItem(item)" :disabled="locked">
          mdi-delete
        </v-icon>
      </template>
    </v-data-table>
  </v-card>
</template>

<script>
import validationMixin from "@/mixins/validation";

export default {
  props: ["_turnos", "_salas", "locked"],
  mixins: [validationMixin],
  data() {
    return {
      editedItem: { name: null, dias_sem_ultima_aula: 0, ordem: 0 },
      defaultItem: { name: null, dias_sem_ultima_aula: 0, ordem: 0 },
      dialogDelete: false,
      dialog: false,
      formTitle: null,
      headers: [
        { text: "Nome", value: "name" },
        { text: "Dias sem última aula", value: "dias_sem_ultima_aula" },
        { text: "Ordenação", value: "ordem" },
        { text: "Ações", value: "actions", sortable: false, width: "10%" },
      ],
    };
  },
  watch: {
    dialog(val) {
      val || this.close();

      if (val === true) {
        this.resetValidation();
      }
    },
    dialogDelete(val) {
      val || this.closeDelete();
    },
  },
  computed: {
    salas() {
      return this._salas.filter((sala) => sala.turno === this.turnoSelecionado);
    },
    turnoSelecionado: {
      get() {
        return this.$store.state.selectedPeriod;
      },
      set(value) {
        this.$store.dispatch("setSelectedPeriod", value);
      },
    },
  },
  methods: {
    editItem(item) {
      this.editedIndex = this.salas.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    deleteItem(item) {
      this.editedIndex = this.salas.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialogDelete = true;
    },

    deleteItemConfirm() {
      this.$emit(
        "delete",
        this.turnoSelecionado,
        this.salas[this.editedIndex].name
      );
      this.closeDelete();
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      if (!this.validate()) {
        return;
      }

      this.editedItem.turno = this.turnoSelecionado;
      this.$emit(
        "save",
        this.salas[this.editedIndex]
          ? this.salas[this.editedIndex].name
          : undefined,
        this.editedItem
      );
      this.close();
    },
  },
};
</script>

